
.admin-input {
  width: 250px !important;
  margin: 5px;
}

.admin-component {
  padding: 10px;
}

.required {
  border-color: red;
}

.error {
  color: red;
  font-style: italic;
  font-size: smaller;
}

.dashboard {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  width: 100%;
}

.updating {
  cursor: progress;
}

.spacer {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.dashboardHeader {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #fff), to(#ccefeb));
    background-image: -webkit-linear-gradient(top, #fff 0, #ccefeb 100%);
    background-image: -o-linear-gradient(top, #fff 0, #ccefeb 100%);
    background-image: linear-gradient(to bottom, #fff 0, #ccefeb 100%);
    border-radius: 4px;
    border-style: solid;
    border-width: 1px;
    border-color: #DDD;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
}

.dashboardFooter {
    -ms-flex-negative: 0;
        flex-shrink: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    background-image: -webkit-gradient(linear, left top, left bottom, color-stop(0, #fff), to(#ccefeb));
    background-image: -webkit-linear-gradient(top, #fff 0, #ccefeb 100%);
    background-image: -o-linear-gradient(top, #fff 0, #ccefeb 100%);
    background-image: linear-gradient(to bottom, #fff 0, #ccefeb 100%);
    border-radius: 4px;
    padding: 10px;
    border-style: solid;
    border-width: 1px;
    border-color: #DDD;
}

.headerTitle {
    font-weight: normal;
}

.headerText {
    position: relative;
    vertical-align: middle;
    padding: 10px;
}

.footerText {
    position: relative;
    vertical-align: middle;
}

.dashboardBody {
    padding: 20px;
    font-size: large;
}

.menuItem {
}

.selected {
  background-image: -webkit-gradient(linear, left bottom, left top, color-stop(0, #eee), to(#bbb));
  background-image: -webkit-linear-gradient(bottom, #eee 0, #bbb 100%);
  background-image: -o-linear-gradient(bottom, #eee 0, #bbb 100%);
  background-image: linear-gradient(to top, #eee 0, #bbb 100%);
  border-style: solid;
  border-width: 0px 1px 0px 1px;
  border-color: #DDD;
}

.waiting-modal {
  position: absolute;
  top: 45%;
  left: 45%;
  z-index: 100;
}

.exit-button {
    cursor: pointer;
    color: #337ab7;
    margin-top: 10px;
    margin-right: 10px;
}

@media (min-width: 768px) {
  .homeTitle {
    font-weight: bold;
    font-size: 3em;
  }
}

@media (max-width: 767px) {
  .homeTitle {
    font-weight: bold;
    font-size: 2em;
  }
}

@media (min-width: 768px) {
  .homeBody {
    font-size: 21px;
  }
}

@media (max-width: 767px) {
  .homeBody {
    font-size: 16px;
  }
}

.loader {
    border: 6px solid rgb(50,50,50,.8);
    border-top: 6px rgb(50,50,50,.35);
    border-radius: 45%;
    width: 40px;
    height: 40px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



